export function getRandomWord() {
  let enigma = [];
  const randomWord =  words[Math.floor(Math.random()*words.length)];
  const duplaLetter = ['cs','gy','ny','sz','ly','ty','zs'];
  for (let i = 0; i < randomWord.length; i++) {
    if (duplaLetter.includes(randomWord.substr(i, 2))) {
      enigma.push(randomWord.substr(i, 2));
      i++;
    } else if (randomWord.substr(i, 3) === 'dzs') {
      enigma.push(randomWord.substr(i, 3));
      i = i+2;
    } else {
      enigma.push(randomWord.substr(i, 1));
    }
  }
  return enigma;
}

export function guessValid(guess) {
  return words.includes(guess);
}

